<template>
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="12"><div style="text-align:left;margin:0 auto;"><span style="font-size:18px">{{sentences}}</span></div></el-col>
          <el-col :span="12"><div style="text-align:right">
            <el-button size="small">导出</el-button>
            </div>
          </el-col>
        </el-row>
      </el-header>
      <el-container>
        <el-main>
          <template>
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
              <el-form-item label="姓名">
                <el-input v-model="formInline.user" placeholder="姓名"></el-input>
              </el-form-item>
              <el-form-item label="活动区域">
                <el-select v-model="formInline.region" placeholder="活动区域">
                  <el-option label="区域一" value="shanghai"></el-option>
                  <el-option label="区域二" value="beijing"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
            <div class="btnRight">
                <el-button type="primary" @click="onSubmit">查询</el-button>
                <el-button type="reset" @click="onSubmit">重置</el-button>
            </div>
          </template>
          <template>
            <el-table :data="tableData" size="small" stripe border style="width:100%" height="616px" :header-cell-style="{
      'background-color': '#e0efff',
      'color': '#000'
  }">
              <el-table-column prop="date"
                label="日期"
                width="150">
              </el-table-column>
              <el-table-column
                prop="name"
                label="姓名"
                width="120">
              </el-table-column>
              <el-table-column
                prop="province"
                label="省份"
                width="120">
              </el-table-column>
              <el-table-column
                prop="city"
                label="市区"
                width="120">
              </el-table-column>
              <el-table-column
                prop="address"
                label="地址"
                width="300">
              </el-table-column>
              <el-table-column
                prop="zip"
                label="邮编"
              >
              </el-table-column>
              <el-table-column
                fixed="right"
                label="操作"
                width="100">
                <template slot-scope="scope">
                  <el-button
                    @click.native.prevent="deleteRow(scope.$index, tableData)"
                    type="text"
                    size="small">
                    移除
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </el-main>
      </el-container>
      <el-footer align="right">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="1000">
        </el-pagination>
      </el-footer>
    </el-container>
</template>
<script>
// import yqdb from '../jsonApi/dbjson.js'
// console.log(yqdb)
export default {
  components: {
  },
  data: function () {
    return {
      msg: 'helloWord',
      tableData: [],
      formInline: {
        user: '',
        region: ''
      },
      sentences: ''
    }
  },
  methods: {
    onSubmit () {
      this.$http.get('/holiday/get').then((response) => {
        this.tableData = response.data.data.list
      }).catch(function (error) {
        console.log(error)
      })
    },
    test () {
      // this.$data.msg = 'eeeeeeeeeee'
      this.msg = 'bbbbbbbbb'
    }
  },
  beforeCreate: function () {
    console.log('1:' + 'beforeCreate创建实例之前执行代码')
  },
  created: function () {
    // console.log('2:' + 'created钩子可以用来在一个实例被创建之后执行代码')
    this.$http.get('https://api.apiopen.top/api/sentences').then((response) => {
      this.sentences = response.data.result.name
    }).catch(function (error) {
      console.log(error)
    })
  },
  beforeMount: function () {
    console.log('3:' + 'beforeMount')
  },
  mounted: function () {
    console.log('4:' + 'mounted')
  },
  beforeDestroy: function () {
    console.log('5:' + 'beforeDestroy')
  },
  destroyed: function () {
    console.log('6:' + 'destroyed')
  }
}
</script>
<style lang="less" scoped>
  .el-header{
    background-color: #ffffff;
    line-height: 48px;
    border-bottom-width:1px;
    border-bottom-style:solid;
    border-bottom-color: #d9d9d9;
  }
  .el-footer{
    background-color: #ffffff;
    line-height: 48px;
  }
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }
  .el-main {
    background-color: #ffffff;
    color: #333;
    text-align: left;
  }
   body > .el-container {
    height: 600px;
  }
  .mheader{
    background-color: #e0efff;
  }
  .demo-form-inline {
    display: inline-block;
  }
  .btnRight{
    right:40px;
    display:inline;
    position: absolute;
  }
</style>
